<template>
  <div class="avatar MerchantAvatar" :class="[level]">
    <van-image :src="$root.getImageUrl(avatar)" fit="cover" round>
      <div slot="error" class="defaultAvatar size30"></div>
    </van-image>
    <div class="cover"></div>
  </div>
</template>

<script>
export default {
  name: "MerchantAvatar",
  props: {
    level: String,
    avatar: String,
  },
  computed: {
    levelStr() {
      if (this.level) {
        if (this.level === "normal") return "黄金";
        if (this.level === "platinum") return "铂金";
        if (this.level === "diamond") return "钻石";
        if (this.level === "crown") return "皇冠";
      }
    },
  },
};
</script>

