<template>
  <div class="page page-earn">
    <div class="fixedDashBoard">
      <div class="merchant">
        <div class="left">
          <MerchantAvatar :avatar="$root.userInfo.avatar" :level="level" />
          <van-button
            class="primary danger btnUpgrade"
            size="mini"
            to="/agreement_detail"
          >
            {{ levelStr }}
          </van-button>
        </div>
        <div class="right">
          <van-field
            :value="
              $root.cryptoMerchantInfo.businessAssureBalance
                | amountFormat({ places: 4 })
            "
            class="deposit"
            label="保证金(USDT):"
            readonly
          >
            <template #button>
              <Refresh
                class="refreshBalance"
                :loading="balanceLoadings.loading1"
                @click="refreshBalance('loading1')"
              />
              <van-button
                to="/deposit_transfer/deposit_transfer_in"
                type="primary"
                plain
                hairline
                class="btnField"
                size="mini"
              >
                划转
              </van-button>
            </template>
          </van-field>
          <van-field
            :value="
              $root.cryptoMerchantInfo.interestSettlement
                | amountFormat({ places: 4 })
            "
            class="interest"
            label="利息(USDT):"
            readonly
          >
            <template #button>
              <Refresh
                class="refreshBalance"
                :loading="balanceLoadings.loading2"
                @click="refreshBalance('loading2')"
              />
              <van-button
                type="primary"
                plain
                hairline
                class="btnField"
                size="mini"
                @click="marginInrerest"
              >
                领取
              </van-button>
            </template>
          </van-field>
          <van-field
            :value="$root.cryptoMerchantInfo.businessBalance | amountFormat()"
            class="merchantBalance"
            label="商家余额(V币):"
            readonly
          >
            <template #button>
              <Refresh
                class="refreshBalance"
                :loading="balanceLoadings.loading3"
                @click="refreshBalance('loading3')"
              />
              <div class="space"></div>
            </template>
          </van-field>
        </div>
      </div>
      <div class="actions">
        <van-button
          type="info"
          size="small"
          plain
          to="/trans_in_balance/trans_in_balance_from_blockchain"
        >
          转入
        </van-button>
        <van-button type="info" size="small" plain to="/merchant_balance_transout">转出</van-button>
        <van-button class="primary" size="small" to="/sale?m=1">开始赚钱</van-button>
      </div>
      <van-sticky offset-top="12.267vw">
        <van-dropdown-menu class="border_bottom_1px">
          <van-dropdown-item ref="payment_method" title="收款方式">
            <van-cell :border="false" class="selections">
              <van-checkbox-group v-model="query.paymentType">
                <van-checkbox :name="PAYMENT_METHOD.BANK" class="btn">
                  <van-button slot="icon" class="btn"> 银行卡转账</van-button>
                </van-checkbox>
                <van-checkbox :name="PAYMENT_METHOD.WE_CHAT" class="btn">
                  <van-button slot="icon" class="btn"> 微信转账</van-button>
                </van-checkbox>
                <van-checkbox :name="PAYMENT_METHOD.ALI" class="btn">
                  <van-button slot="icon" class="btn"> 支付宝转账</van-button>
                </van-checkbox>
              </van-checkbox-group>
            </van-cell>
            <van-cell class="btns">
              <van-button class="cancel" type="danger" @click="resetPaymentMethod">
                重置
              </van-button>
              <van-button class="confirm" type="primary" @click="load('payment_method')">
                确定
              </van-button>
            </van-cell>
          </van-dropdown-item>
          <van-dropdown-item ref="status" title="状态">
            <van-cell :border="false" class="selections">
              <div class="group">
                <van-checkbox v-model="query.isTransacting" class="btn">
                  <van-button slot="icon" class="btn"> 已发起交易</van-button>
                </van-checkbox>
                <van-checkbox v-model="query.isHanging" class="btn">
                  <van-button slot="icon" class="btn"> 挂单中</van-button>
                </van-checkbox>
              </div>
            </van-cell>
            <van-cell class="btns">
              <van-button class="cancel" type="danger" @click="resetStatus"> 重置 </van-button>
              <van-button class="confirm" type="primary" @click="load('status')"> 确定 </van-button>
            </van-cell>
          </van-dropdown-item>
          <van-dropdown-item ref="split_status" title="是否可拆分">
            <van-cell :border="false" class="selections">
              <van-radio-group v-model="query.isSplit" @change="(v) => onChangeSplitStatus(v)">
                <van-radio :name="1" class="btn">
                  <van-button slot="icon" class="btn" @click="cancelSelect(1)"> 可拆分 </van-button>
                </van-radio>
                <van-radio :name="0" class="btn">
                  <van-button slot="icon" class="btn" @click="cancelSelect(0)">
                    不可拆分
                  </van-button>
                </van-radio>
              </van-radio-group>
            </van-cell>
          </van-dropdown-item>
        </van-dropdown-menu>
      </van-sticky>
    </div>
    <van-list
      v-model="loading"
      :error-text="errorText"
      :error.sync="error"
      :finished="finished"
      finished-text="暂无更多数据"
      @load="search"
    >
      <HangOrderItem v-for="(item, index) in list" :key="index" :dt="item" />
    </van-list>
  </div>
</template>

<script lang="ts">
import { marginInrerest, myPendingOrderList } from "@/apis/crypto";
import MerchantAvatar from "@/components/MerchantAvatar.vue";
import { PAYMENT_METHOD } from "@/vendor/enum";
import tools from "@/vendor/tools";
import Vue from "vue";
import { HangOrderItem } from "../home/components";
import Refresh from "@/components/Refresh.vue";

export default Vue.extend({
  components: { HangOrderItem, MerchantAvatar, Refresh },
  data() {
    return {
      PAYMENT_METHOD,
      list: [],
      loading: false,
      error: false,
      finished: false,
      errorText: "加载失败，点击重试",
      page: 1,
      pageSize: 20,

      query: {
        isTransacting: false,
        isHanging: false,
        isSplit: "",
        paymentType: [],
      },

      balanceLoadings: {
        loading1: false,
        loading2: false,
        loading3: false,
      },
    };
  },
  computed: {
    level() {
      const level = this.$root.cryptoMerchantInfo.level.matchingLevel;
      if (level === "ORDINARY") return "normal";
      if (level === "Platinum") return "platinum";
      if (level === "Diamond") return "diamond";
      if (level === "CROWN") return "crown";
      return '普通'
    },
    levelStr() {
      if (this.level) {
        console.log('level==',this.level);
        if (this.level === "normal") return "黄金";
        if (this.level === "platinum") return "铂金";
        if (this.level === "diamond") return "钻石";
        if (this.level === "crown") return "皇冠";
        return '普通';
      }
    },
  },
  watch: {
    query: {
      deep: true,
      handler() {
        this.page = 1;
        this.list = [];
      },
    },
  },
  created() {
    this.$root.getCryptoMerchantInfo();
  },
  methods: {
    async refreshBalance(loading) {
      this.balanceLoadings[loading] = true;
      await this.$root.getCryptoMerchantInfo();
      this.balanceLoadings[loading] = false;
    },
    search() {
      myPendingOrderList(
        this.$tls.processParams(
          {
            page: this.page,
            pageSize: this.pageSize,
            isSplit: this.query.isSplit,
            paymentType: this.query.paymentType.reduce(
              (previous, current) => Number(previous) + Number(current),
              0
            ),
            inProgress: this.query.isTransacting ? 1 : "",
            status: this.query.isHanging ? 1 : "",
          },
          ["shake"]
        )
      ).then(({ ok, data, msg }) => {
        this.loading = false;
        if (ok) {
          if (this.page == 1) {
            this.list = data.list || [];
          } else {
            this.list.push(...(data.list || []));
          }
          this.finished = this.page * this.pageSize >= data.total;
          this.page += 1;
        } else {
          this.error = true;
          this.errorText = msg;
        }
      });
    },
    resetPaymentMethod() {
      this.query.paymentType = [];
      this.load("payment_method");
    },
    resetStatus() {
      this.query.isTransacting = false;
      this.query.isHanging = false;
      this.load("status");
    },
    load(key) {
      this.page = 1;
      this.$refs[key].toggle(false);
      this.search();
    },
    onChangeSplitStatus(v) {
      if (tools.isNumber(v)) {
        this.load("split_status");
      }
    },
    cancelSelect(v) {
      if (this.query.isSplit === v) {
        setTimeout(() => {
          this.query.isSplit = "";
          this.load("split_status");
        }, 50);
      }
    },
    async marginInrerest() {
      const { ok, data, msg } = await marginInrerest();
      if (ok) {
        this.$toast.success("领取成功");
      } else {
        this.$toast.fail(msg);
      }
    },
  },
});
</script>

